import { notFound } from 'next/navigation'
import { getProductById, getRelatedProducts } from '@/lib/data'
import { formatPrice } from '@/lib/utils'
import Image from 'next/image'
import Link from 'next/link'
import AddToCartButton from '@/components/AddToCartButton'
import ProductCard from '@/components/ProductCard'
import BackToTop from '@/components/BackToTop'

interface ProductPageProps {
  params: {
    id: string
  }
}

export default function ProductPage({ params }: ProductPageProps) {
  const product = getProductById(params.id)
  const relatedProducts = getRelatedProducts(params.id, 4)
  
  if (!product) {
    notFound()
  }
  
  return (
    <div className="min-h-screen bg-white">
      {/* Header - Design Guidelines */}
      <header className="header-bg shadow-lg">
        <div className="max-w-[1200px] mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <div className="flex items-center">
              <Link href="/" className="header-logo hover:text-red-600 transition-colors">
                TechStore
              </Link>
            </div>
            
            {/* Search Input - Design Guidelines */}
            <div className="flex-1 max-w-2xl mx-8">
              <div className="relative">
                <input
                  type="search"
                  placeholder="What are you looking for?"
                  className="search-input w-full"
                />
                <div className="absolute inset-y-0 right-0 flex items-center pr-3">
                  <svg className="h-5 w-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </div>
              </div>
            </div>
            
            {/* Header Actions - Design Guidelines */}
            <div className="flex items-center space-x-6">
              <Link 
                href="/"
                className="header-nav-link hover:text-red-600 flex items-center gap-2 px-4 py-2 rounded-lg"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                </svg>
                Back to Products
              </Link>
              <button className="header-nav-link hover:text-red-600 px-3 py-2 rounded-lg">
                Account
              </button>
              <button className="header-nav-link hover:text-red-600 px-3 py-2 rounded-lg">
                Wishlist
              </button>
              <button className="header-nav-link hover:text-red-600 px-3 py-2 rounded-lg relative">
                Cart
                <span className="absolute -top-2 -right-2 bg-red-600 text-white text-xs rounded-full h-5 w-5 flex items-center justify-center">
                  0
                </span>
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Navigation Breadcrumb - Design Guidelines */}
      <nav className="breadcrumb-bg border-b border-gray-300">
        <div className="max-w-[1200px] mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center h-12">
            <Link href="/" className="breadcrumb-text hover:text-red-600">
              Home
            </Link>
            <span className="mx-2 breadcrumb-text">/</span>
            <Link href="/" className="breadcrumb-text hover:text-red-600">
              Products
            </Link>
            <span className="mx-2 breadcrumb-text">/</span>
            <span className="breadcrumb-current font-medium">{product.name}</span>
          </div>
        </div>
      </nav>

      <div className="max-w-[1200px] mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="product-card-bg rounded-lg overflow-hidden shadow-lg">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 padding-card">
            {/* Product Image Container - Design Guidelines */}
            <div className="product-container aspect-square relative shadow-inner">
              <Image
                src={product.image}
                alt={`${product.name} product image`}
                fill
                className="object-contain drop-shadow-lg"
                sizes="(max-width: 1024px) 100vw, 50vw"
              />
            </div>
            
            {/* Product Details - Design Guidelines */}
            <div className="space-y-6">
              <div>
                {/* H1 Page Title - Design Guidelines */}
                <h1 className="text-h1 mb-4 text-black">
                  {product.name}
                </h1>
                {/* Product Description */}
                <p className="text-body-large secondary-text">
                  {product.description}
                </p>
              </div>
              
              {/* Rating - Design Guidelines */}
              <div className="product-card-bg flex items-center space-x-4 padding-card rounded-lg border border-gray-300">
                <div className="flex items-center">
                  {[...Array(5)].map((_, i) => (
                    <span 
                      key={i} 
                      className={`text-xl ${
                        i < Math.floor(product.rating.average) ? 'star-rating-active' : 'star-rating-inactive'
                      }`}
                    >
                      ★
                    </span>
                  ))}
                </div>
                <span className="text-body-medium secondary-text">
                  ({product.rating.average.toFixed(1)}) · {product.rating.count} reviews
                </span>
              </div>
              
              {/* Price - Design Guidelines */}
              <div className="padding-card rounded-lg shadow-lg border-2 border-red-600">
                <span className="product-price-large">
                  {formatPrice(product.price)}
                </span>
              </div>
              
              {/* Stock Status & Add to Cart - Design Guidelines */}
              <div className="space-y-4 product-card-bg padding-card rounded-lg border border-gray-300">
                <div className="flex items-center justify-between">
                  <span className="text-body-large font-medium text-black">
                    Availability:
                  </span>
                  <span 
                    className={`px-3 py-1.5 rounded-full text-xs font-semibold ${
                      product.inStock 
                        ? 'success-text bg-green-100' 
                        : 'text-red-600 bg-red-100'
                    }`}
                  >
                    {product.inStock ? 'In Stock' : 'Out of Stock'}
                  </span>
                </div>
                
                <AddToCartButton
                  productId={product.id}
                  productName={product.name}
                  inStock={product.inStock}
                />
              </div>
              
              {/* Product Details Information Panel - Design Guidelines */}
              <div className="product-card-bg padding-card rounded-lg border border-gray-300">
                <h3 className="text-h3 mb-4 text-black">
                  Product Details
                </h3>
                <dl className="space-y-3">
                  <div className="flex justify-between items-center py-2 border-b border-gray-300">
                    <dt className="text-body-medium secondary-text">Category:</dt>
                    <dd className="capitalize font-medium text-black">
                      {product.categoryId}
                    </dd>
                  </div>
                  <div className="flex justify-between items-center py-2">
                    <dt className="text-body-medium secondary-text">Product ID:</dt>
                    <dd className="font-mono text-body-medium text-black">
                      {product.id}
                    </dd>
                  </div>
                </dl>
              </div>
            </div>
          </div>
        </div>

        {/* Related Products Section - Design Guidelines */}
        {relatedProducts.length > 0 && (
          <div className="mt-16">
            <h2 className="text-h2 mb-8 text-black">
              Related Products
            </h2>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-grid">
              {relatedProducts.map((relatedProduct) => (
                <ProductCard 
                  key={relatedProduct.id} 
                  product={relatedProduct}
                  className="h-full"
                />
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
